:root{
  --black : #222222;
  --orange : #FFC04A;
  --red : #FF0000;
  --white: #FFFFFF;
  --brown: #2D2D2D;
  --padding: 0px 15%;
  --margin: 0 0;

}

*{
  padding:0px;
  margin:0px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
}

header{
  position: fixed;
  top: 0px;
  left:0px;
  z-index: 99;
  height: 70px;
  width: 100%;
  background-color: var(--black);
  padding: var(--padding);
  box-sizing: border-box;
 }
nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: fit-content;
  margin: 0px;
  width: 100%;
  padding-top: 15px;
}

nav  img {
  height: 30px;
 
  float: left;
  
}

nav ul{
  list-style: none;
  float: right;
  display: flex;
  flex-direction: row;
  
  
  

}
nav ul li{
display: inline;
text-transform: uppercase;
margin-right : 10px;
color: var(--white);
box-sizing: border-box;

}

li a, .dropbtn {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 0px 16px;
  text-decoration: none;
}

li a:hover, .dropdown:hover .dropbtn {
  background-color:var(--black);
}

li.dropdown {
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: var(--black);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: var(--white);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {background-color: var(--var);}

.dropdown:hover .dropdown-content {
  display: block;
}
.jumb{
  position: static;
  margin-top: 70px; ;
}

#dress{
  position: absolute;
  top: 70px;
  left: 0px;
  width: 100%;
  height: 600px;
  background-color: var(--orange);
  margin : var(--margin);
  padding: var(--padding);
  padding-top: 3%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row; 
  justify-content: space-between;
 
}

#dress-left img{
  height: 85%;
  margin-left:-3%;
  transition: margin-left 2s ease-in;

}
#dress-right{
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 1.4em;
  line-height: 2em;
 
  
}
#dress-right h1 > a{
  color: var(--white);
  text-decoration: none;


}
#dress-right p{
  color: var(--white);
}
#dress-right input{
  padding:15px;
  background-color: var(--brown);
  color: var(--white);
  margin-top: 10px;
  font-size: 0.7em;
}

#comp{
  position: relative;
  top:180px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: space-around;
}